<template>
    <div class="row">
        <Loading :active.sync="isLoading"
                 :is-full-page="fullPage"
                 :color="color"
        ></Loading>
        <div class="col s10 offset-s1">
            <h3>Параметры запроса</h3>
            <form @submit.prevent="submitHandler">
                <div class="input-field col s6">
                    <input v-model="src" id="src" type="text" class="validate">
                    <label for="src">Кто звонил</label>
                </div>
                <div class="input-field col s6">
                    <input v-model="dst" id="dst" type="text" class="validate">
                    <label for="dst">Куда звонил</label>
                </div>
                <div class="input-field col s6">
                    <input v-model="dateFrom" id="datefrom" type="text" ref="datepicker1">
                    <label for="datefrom">Дата начала поиска</label>
                </div>
                <div class="input-field col s6">
                    <input v-model="dateTo" id="dateto" type="text" ref="datepicker2">
                    <label for="dateto">Дата конца поиска</label>
                </div>
                <div class="col s6 d-flex">
                    <button class="btn" type="submit">Сделать запрос</button>
                </div>
                <div class="input-field col s6">
                    <select ref="select" v-model="chunkSize">
                        <option value="25" selected>25</option>
                        <option value="50">50</option>
                        <option value="100">100</option>
                        <option value="150">150</option>
                    </select>
                    <label>Количество записей на страницу</label>
                </div>
            </form>
        </div>
        <div class="col s12">
            <h6>Число записей: <b>{{callsNumber}}</b></h6>
            <h6>Количество минут разговора: <b>{{callsMinutes}}</b></h6>
            <label>
                <input type="checkbox" v-model="isOnlyAnswered"/>
                <span>Только отвеченные</span>
            </label>
            <table v-if="calls.length" class="highlight centered">
                <thead>
                <tr>
                    <th>ID</th>
                    <th>Время звонка</th>
                    <!--                    <th>CID</th>-->
                    <th>Кто звонил</th>
                    <th>Куда звонили</th>
                    <th>Обработка звонка</th>
                    <th>Общая продолжительность звонка</th>
                    <th>Продолжительность разговора</th>
                    <th>Статус звонка</th>
                    <th>Исходящий канал</th>
                    <th>Входящий канал</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(call, idx) of displayByChunksWithFilter" :key="call.id" :style="call.color">
                    <td><b>{{page * chunkSize + 1 + idx }}</b></td>
                    <td>{{call.calldate}}</td>
                    <!--                    <td>{{call.clid.split('"')[1] ? call.clid.split('"')[1] : '-'}}</td>-->
                    <td>{{call.src}}</td>
                    <td>{{call.dst}}</td>
                    <td>{{call.lastdata}}</td>
                    <td>{{call.duration}} секунд</td>
                    <td>{{call.billsec}} секунд</td>
                    <td>{{call.disposition}}</td>
                    <td>{{call.channel.split('-')[0]}}</td>
                    <td>{{call.dstchannel.split('-')[0]}}</td>
                </tr>
                </tbody>
            </table>
            <p v-else>Нет звонков</p>
        </div>
        <div class="col s12 d-flex" v-if="calls.length">
            <ul class="pagination">
                <!--        <li class="active"><a href="#!">1</a></li>-->
                <li class="waves-effect"
                    @click="page = idx"
                    v-bind:class="{active: idx === page}"
                    v-for="(chunk, idx) in [...Array(Math.ceil(callsNumber/chunkSize))]">
                    <a href="#!">{{idx +1}}</a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

    import Loading from 'vue-loading-overlay';

    export default {
        name: 'Home',

        components: {Loading},

        data: () => ({
            src: null,
            dst: null,
            dateFrom: null,
            dateTo: null,
            isLoading: false,
            fullPage: true,
            color: '#1565c0',
            page: 0,
            chunkSize: 25,
            isOnlyAnswered: false
        }),
        computed: {
            calls() {
                this.isLoading = false
                this.page = 0
                return this.$store.getters.calls
            },
            callsMinutes() {
                return this.$store.getters.callsMinutes
            },
            callsNumber() {
                if (this.isOnlyAnswered) {
                    let temp = this.calls.filter(c => {
                        return c.disposition === 'ANSWERED'
                    })
                    return temp.length
                } else {
                    return this.$store.getters.callsNumber
                }
            },
            displayByChunksWithFilter() {
                let tempCalls = []
                if (this.isOnlyAnswered) {
                    tempCalls = this.calls.filter(c => {
                        return c.disposition === 'ANSWERED'
                    })
                    return tempCalls.slice(this.page * this.chunkSize, (this.page + 1) * this.chunkSize)
                }
                return this.calls.slice(this.page * this.chunkSize, (this.page + 1) * this.chunkSize)
            }
        },
        mounted() {
            this.dateFrom = M.Datepicker.init(this.$refs.datepicker1, {
                format: 'yyyy-mm-dd',
                defaultDate: new Date(),
                setDefaultDate: true,
                showClearBtn: true
            });
            this.dateTo = M.Datepicker.init(this.$refs.datepicker2, {
                format: 'yyyy-mm-dd',
                defaultDate: new Date(),
                setDefaultDate: true,
                showClearBtn: true
            });
            M.FormSelect.init(this.$refs.select, {});
        },
        methods: {
            submitHandler() {
                this.isLoading = true
                const query = {
                    src: this.src,
                    dst: this.dst,
                    dateFrom: this.dateFrom.date ? this.dateFrom.date.getFullYear() + '-' + parseInt(this.dateFrom.date.getMonth() + 1) + '-' + this.dateFrom.date.getDate() : null,
                    dateTo: this.dateTo.date ? this.dateTo.date.getFullYear() + '-' + parseInt(this.dateTo.date.getMonth() + 1) + '-' + this.dateTo.date.getDate() : null
                }
                this.$store.dispatch('getCalls', query)
            }
        },

        destroyed() {
            if (this.dateFrom && this.dateFrom.destroy) {
                this.dateFrom.destroy()
            }
            if (this.dateTo && this.dateTo.destroy) {
                this.dateTo.destroy()
            }
        }
    }
</script>
<style scoped>
    .d-flex {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .d-flex ul {
        width: fit-content;
    }
</style>